body {
/*border: 1px solid crimson;*/
  font-family: sans-serif;
}

.leaflet-container {
  border: 2px solid crimson;
  margin: 1%;
  height: 600px;
/*  width: inherit;*/
/*  flex-grow: 1;*/

}

.popups{
  font-size: 20px;
}

svg {
/*  border: 1px solid limeGreen;*/
/*  margin: auto;*/
  margin: 1% 0 1% 0;
/*  display: flex;*/
  float: left;
}

g:hover text {
  opacity: 1.0;
  fill: crimson;
  font-size: 22px;
  font-weight: bold;
}

g:hover circle {
  stroke: black;
  stroke-width: 2.5;
  opacity: 1.0;
}

h1 {
  text-align: center;
  text-transform: uppercase;
  color: crimson;
}

h2 {
  text-align: center;
  text-transform: lowercase;
}

select {
  display: block;
  margin: auto;
  font-size: 22px;
  text-align: center;
}

input {
  display:block;
  margin: auto;
/*  margin-bottom: 1%;*/
}




